'use client';

import { useEffect } from 'react';
import { useAccount, useReadContract } from 'wagmi';
import { ConnectButton } from '@rainbow-me/rainbowkit';
import { sdk } from '@farcaster/miniapp-sdk';
import { COLLECTION_ABI } from '@/lib/abi/collection';
import { CONTRACT_ADDRESS } from '@/lib/constants';

export default function MyNftsPage() {
  const { address, isConnected } = useAccount();

  useEffect(() => {
    if (typeof window === 'undefined') return;
    const appUrl = process.env.NEXT_PUBLIC_APP_URL;
    const sameOrigin = !appUrl || window.location.origin === appUrl;
    if (sameOrigin) {
      sdk.actions.ready().catch(console.error);
    }
  }, []);

  const { data: mintedOfMe } = useReadContract({
    address: CONTRACT_ADDRESS,
    abi: COLLECTION_ABI,
    functionName: 'mintedOf',
    args: [address ?? '0x0000000000000000000000000000000000000000'],
  });

  return (
    <main className="min-h-screen bg-neutral-950 text-white">
      <header className="flex items-center justify-between p-4 border-b border-neutral-800">
        <div className="text-lg font-semibold">RACCOON-MINT — My NFT</div>
        <ConnectButton />
      </header>

      <section className="max-w-3xl mx-auto p-6 grid gap-6">
        <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-6">
          <h2 className="text-xl font-bold mb-4">Your Minted NFTs</h2>
          {!isConnected ? (
            <p className="text-sm text-neutral-300">Connect your wallet to see your mints.</p>
          ) : (
            <>
              <p className="text-sm text-neutral-300">
                You minted: <span className="font-mono text-white">{(mintedOfMe as bigint|undefined)?.toString() ?? '0'}</span>
              </p>
              <div className="mt-4">
                <a
                  className="text-cyan-300 hover:underline"
                  href={`https://basescan.org/address/${CONTRACT_ADDRESS}`}
                  target="_blank"
                  rel="noreferrer"
                >
                  View contract on BaseScan →
                </a>
              </div>
            </>
          )}
        </div>

        <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-6">
          <h3 className="font-semibold mb-2">Coming soon</h3>
          <p className="text-sm text-neutral-300">
            We’ll render your owned token metadata/images here once token IDs and metadata endpoint are available.
          </p>
        </div>
      </section>
    </main>
  );
}
